import { Axios } from 'axios'
import {
  VaultReward,
  VaultRewardRoutes,
  VaultRewardsClientModel,
  VaultRewardsSearchQuery,
} from '../vault-reward.model'
import { ApiEntity } from '../../core'

export class VaultRewardsClient
  extends ApiEntity
  implements VaultRewardsClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Search rewards by params
   * @param searchParams - the search vault rewards search params
   * @returns the promise for search rewards
   */
  public search(searchParams?: VaultRewardsSearchQuery) {
    return this._search<VaultReward>(VaultRewardRoutes.v1Search, searchParams)
  }

  /**
   * Search all reward by params
   * @param searchParams - the vault
   * @returns
   */
  public searchAll(searchParams?: VaultRewardsSearchQuery) {
    return this._searchAll<VaultReward>(
      VaultRewardRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List rewards by params
   * @param searchParams - the search vault rewards search params
   * @returns the promise for list rewards
   */
  public list(searchParams?: VaultRewardsSearchQuery) {
    return this._list<VaultReward>(VaultRewardRoutes.v1Search, searchParams)
  }

  /**
   * List all rewards by params
   * @param searchParams - the search vault rewards search params
   * @returns the promise for list rewards
   */
  public listAll(searchParams?: VaultRewardsSearchQuery) {
    return this._listAll<VaultReward>(VaultRewardRoutes.v1Search, searchParams)
  }

  /**
   * Find an reward by params
   * @param searchParams - the search params
   * @returns the promise for find an reward
   */
  public findOne(searchParams?: VaultRewardsSearchQuery) {
    return this._findOne<VaultReward>(VaultRewardRoutes.v1Search, searchParams)
  }
}
